
import Msg from 'vux/src/components/msg/index.vue'

export default {
components: {
Msg,
},
computed: {
loginLink:function () {
if (this.$parent.isNative) {

return  "https://www.maxwon.cn/member";
} else {

return `${this.$parent.h5UrlPrefix}/login?return_uri=${window.location.href}`;
}
},
loginMsgButtons: function () {
return [{
type: 'primary',
text: ' LeapCloud ',
link: this.loginLink
}];
},
},
data() {
return {
title: " h5 "
}
}
}



